/*---------------------------------- Title Galery ----------------------------------*/

.Bg_titleGalery {
    /* background: rgba(0, 92, 230, 0.8); */
    background: #031d36;
    height: 10em;
    margin: auto;
    padding: 2em;
    display: flex;
    justify-content: center;
    align-items: center;
    /* word-break: initial;
    hyphens: auto; */
}

/* .Bar_titleGalery {
    position: absolute;
    width: 100%;
    border-top: solid 6px rgba(14, 186, 209, 0.8);
    border-bottom: solid 6px rgba(14, 186, 209, 0.8);
} */

.titleGalery {
    color: rgb(255, 255, 255);
    font-weight: bold;
}

/*---------------------------------- Cards Galery ----------------------------------*/

/*Estado del producto*/

.circle_item {
    color: transparent;
    width: 10px;
    height: 10px;
    border-radius: 100px;
    margin-right: 5px;
}

.active_item {
    background: #66f115;
}

.low_item {
    background: rgb(255, 136, 0);
}

.not_item {
    background: rgb(255, 0, 0);
}

/* Precios */

.precio_color {
    color: #018599;
    font-weight: bold;
    font-size: 1.3em;
}

/* ______________________________Media queries________________________________ */

@media (max-width: 575.98px) {
    .active_item {
        background: tomato;
    }
    /* Title Galery */
    .Bg_titleGalery {
        height: 100%;
        padding: 0em;
    }
    .titleGalery {
        font-size: 25px;
    }
}

@media (min-width: 576px) and (max-width: 767.98px) {
    .active_item {
        background: forestgreen;
    }
    /* Title Galery */
    .Bg_titleGalery {
        height: 100%;
        padding: 0em;
    }
    .titleGalery {
        font-size: 30px;
    }
}

@media (min-width: 768px) and (max-width: 991.98px) {
    .active_item {
        background: rgb(255, 2, 213);
    }
    /* Title Galery */
    .Bg_titleGalery {
        height: 100%;
        padding: 0em;
    }
    .titleGalery {
        font-size: 35px;
    }
}

@media (min-width: 992px) and (max-width: 1199.98px) {
    .active_item {
        background: rgb(253, 118, 0);
    }
}

